<template>
		<view class="goods-box">
			<template v-for="(item,k) in list">
				<navigator class="goods-li" :url="'/pages/goods/detail?seqId='+item.seqId+'&merchantShopId='+item.merchantShopId" :key="k">
					<image lazy-load class="img scale45" :src="item.mainPicUrl" mode="aspectFill"></image>
					<view class="title-box">
						<view class="tips-box" v-if="item.spuLabel">
							<template v-for="(val,i) in item.spuLabel.split(',')">
								<view class="width106 " :class="{'bg-btn':i==0,'border-btn':i==1}" :key="i">
									{{val}}
								</view>
							</template>
						</view>
						<view class="title">
							{{item.spuTitle}}
						</view>
						<view class="price-box">
							<view class="num font-bold">￥{{item.promptPrice | formatNum}}</view>
							<!-- <view class="people-num">1001人付款</view> -->
						</view>
					</view>
				</navigator>
			</template>
		</view>
</template>

<script>
	export default {
		data() {
			return {
				page_index:1,
				page_size:10,
				no_data:false,
				data_list :[]
			}
		},
		props:{
			list:{
				type:Array,
				default:[{}]
			}
		},
	}
</script>

<style lang="scss">
	.width106 {
		height: auto;
		font-size:20rpx;
		margin: 0 10rpx;
		padding: 6rpx 14rpx;
		
		
	}
.goods-box {
		width: 100%;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		align-items: flex-start;

		.goods-li {
			flex-shrink: 0;
			width: 328rpx;
			background: rgba(255, 255, 255, 1);
			border-radius: 8rpx;
			overflow: hidden;
			margin-bottom: 34rpx;

			.img {
				width: 100%;
				height: 410rpx;
				background: white;
			}
		}
	}
	.title-box {
		width: calc(100% - 40rpx);
		padding: 20rpx 20rpx;
		padding-top: 0;
	
		.tips-box {
			display: flex;
		}
	
		.title {
			font-size: 30rpx;
			margin: 12rpx 0;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
	
		}
	
		.price-box {
			width: 100%;
			display: flex;
			align-items: center;
	
			.num {
				font-size: 32rpx;
				color: $color-primary;
			}
	
			.people-num {
				font-size: 20rpx;
				margin-left: 40rpx;
			}
		}
	}
</style>
